html {
  font: 100%;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

:root {
  --main-bg: hsl(212, 35%, 52%);
  --primary-color: hsl(0, 0%, 100%);
  --footer-text: hsl(0, 0%, 0%);
}

@media (min-width: 48em) {
  h1 {
    font-size: 2.25rem;
    font-weight: normal;
    line-height: 0.75rem;
  }
}

@media (min-width: 48em) {
  h2 {
    font-size: 1.875rem;
    font-weight: normal;
    margin-top: 1.875rem;
  }
}

@media (min-width: 48em) {
  p {
    font-size: 0.8rem;
  }
}

body {
  font-family: Arial, Helvetica, sans-serif;
}
@media (min-width: 48em) {
  body {
    max-width: 36.25rem;
    margin: 0 auto;
  }
}

@media (min-width: 48em) {
  .header {
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
    align-items: center;
    padding: 3.125rem 6.25rem 1.5625rem 1.5625rem;
    transform: translateX(-4.6875rem);
    width: 80%;
  }
}
.header__title-line {
  height: 6.25rem;
  padding: 0 1.25rem 0 1.25rem;
  align-self: center;
  transform: translateY(0.625rem);
}
@media (max-width: 47.9375em) {
  .header__title-line {
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
    align-items: center;
  }
}
.header__logo {
  transform: translateX(-0.625rem);
  cursor: pointer;
}
@media (min-width: 48em) {
  .header__logo {
    transform: scale(0.7);
  }
}
.header__icon {
  width: 15.625rem;
  height: 3.125rem;
  transform: translate(0.3125rem, 1.375rem);
}
@media (max-width: 47.9375em) {
  .header__icon {
    transform: translate(-0.3125rem, 0.1875rem);
  }
}
.header__menu-button {
  display: none;
}
@media (max-width: 47.9375em) {
  .header__menu-button {
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
    align-items: center;
    background: transparent;
    border: none;
    width: 3.125rem;
    height: 3.125rem;
    cursor: pointer;
    position: relative;
  }
}
@media (max-width: 47.9375em) {
  .header__menu-icon, .header__menu-icon::before, .header__menu-icon::after {
    background-color: var(--footer-text);
    height: 0.3125rem;
    border-radius: 0.3125rem;
    width: 2.5rem;
    position: absolute;
  }
}
@media (max-width: 47.9375em) {
  .header__menu-icon::before, .header__menu-icon::after {
    content: "";
  }
}
@media (max-width: 47.9375em) {
  .header__menu-icon::before {
    transform: translate(-1.25rem, -0.75rem);
  }
}
@media (max-width: 47.9375em) {
  .header__menu-icon::after {
    transform: translate(-1.25rem, 0.75rem);
  }
}

@media (max-width: 47.9375em) {
  :is(header:hover, header:focus-within) .header__menu-icon {
    background-color: transparent;
    transform: rotate(720deg);
  }
  :is(header:hover, header:focus-within) .header__menu-icon::before {
    transform: rotate(45deg);
  }
  :is(header:hover, header:focus-within) .header__menu-icon::after {
    transform: rotate(-45deg);
  }
  :is(header:hover, header:focus-within) nav {
    display: block;
    background-color: aliceblue;
  }
}
nav ul {
  list-style-type: none;
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  gap: 2.0625rem;
}
nav li {
  cursor: pointer;
}
nav a {
  display: block;
  text-align: center;
  text-transform: uppercase;
  width: 80%;
  margin: auto;
  font-size: 1rem;
  line-height: 1.83625rem;
  color: var(--main-bg);
}
nav a:any-link {
  text-decoration: none;
}
nav a:hover, nav a:focus {
  transform: scale(1.2);
  transition: all 0.3s;
}
@media (max-width: 47.9375em) {
  nav {
    display: none;
    transform-origin: top center;
    animation: showMenu 0.5s ease-in-out forwards;
  }
  nav ul {
    flex-flow: column nowrap;
  }
  nav li {
    padding: 0.5rem;
    border-top: 0.0625rem solid var(--footer-text);
  }
  nav a:any-link {
    color: var(--main-bg);
  }
  @keyframes showMenu {
    0% {
      transform: scale(0);
    }
    80% {
      transform: scale(1.2);
    }
    100% {
      transform: scale(1);
    }
  }
}

@media (min-width: 48em) {
  .container {
    transform: translateY(-3rem);
  }
}
.container__top {
  background-color: var(--main-bg);
  color: var(--primary-color);
  margin: 0;
  padding: 1.25rem;
  text-align: start;
}
@media (min-width: 48em) {
  .container__top {
    text-align: center;
    padding: 2.5rem;
  }
}
.container__message {
  margin-top: 1.875rem;
}
@media (min-width: 48em) {
  .container__message {
    margin-top: 3.4375rem;
  }
}
.container__button {
  cursor: pointer;
  margin: 1.875rem 0;
  height: 1.5625rem;
  background-color: var(--primary-color);
  border: none;
  border-radius: 0.125rem;
  color: var(--main-bg);
  font-weight: bold;
}
.container__button:hover, .container__button:focus {
  transform: scale(1.2);
  transition: all 0.3s;
}
.container__divider {
  border-bottom: 0.00625rem solid gray;
  transform: translateY(1.25rem);
  width: 98%;
}

.footer {
  margin: 2.5rem 1.25rem 1.25rem;
  display: flex;
  flex-flow: column nowrap;
  justify-content: flex-start;
  text-align: left;
  gap: 0.9375rem;
}
@media (min-width: 48em) {
  .footer {
    margin: 5rem 3.75rem 1.25rem;
    transform: translateY(-4.0625rem);
    text-align: center;
  }
}
.footer__logos {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 0.9375rem;
  place-content: center;
}
@media (min-width: 48em) {
  .footer__logos {
    justify-content: center;
    justify-self: center;
    margin-left: 5.625rem;
    margin-right: 5.625rem;
  }
}
.footer__icon {
  width: 2.1875rem;
  height: 2.1875rem;
  border-radius: 50%;
  margin: 0;
  display: block;
  cursor: pointer;
  justify-self: center;
}
.footer__address {
  margin-top: 1.25rem;
  text-align: left;
}
@media (min-width: 48em) {
  .footer__address {
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
    text-align: center;
  }
}
.footer__link {
  cursor: pointer;
}
.footer__text {
  font-size: 0.8rem;
}